.top_header {
  background-color: #ffffff;
}

.logo img {
  width: 12rem;
}
.navigation_section {
  display: flex;
  align-items: center;
  .nav_bar {
    ul {
      display: flex;
      align-items: center;
    }
    li {
      margin: 0 1.5rem;
      font-size: 1rem;
      text-transform: capitalize;
    }
    a {
      color: black;
    }
  }
}

.user_section {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-around;
}
.user_section {
  img {
    height: 2rem;
    width: 2rem;
  }
  button {
    text-transform: capitalize;
    background-color: #124d4f;
    border: none;
    color: white;
    padding: 8px 18px;
    font-size: 1rem;
    margin: 0 6px;
    border-radius: 0.6rem;
    cursor: pointer;
    outline: none;
  }
  span {
    color: rgb(123, 123, 211);
  }
  .dropbtn {
    background-color: transparent;
    border: 1.5px solid #124d4f;
    color: #124d4f;
    text-transform: Capitalize;
  }
  .fa {
    margin-left: 0.5rem;
  }
}
